<template>
  <div class="container">
    <van-loading v-if="loadingShow" class="my-loading" color="#fe8f01" />
    <div class="top">
      <div class="show-week">
        <van-checkbox class="my-checkbox" v-model="check">显示本周</van-checkbox>
        <span>{{currentDate}}</span>
      </div>
      <div class="box" @click="addWorkDaily">
        <img width="50%" src="../../../assets/images/work/add_icon.png" />
        <span>添加工作日报</span>
      </div>
    </div>
    <div class="main">
      <van-list
        v-model="loading"
        :finished="finished"
        :immediate-check="false"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
        finished-text="没有更多了"
        @load="onLoad"
        v-show="!thisWeek"
      >
      <div class="panel" v-for="(item, index) in dispalyWorkDaily"
           :key="index" @click="showWorkDailyById(item.daily_id)">
        <div class="panel-title">
          <img class="ribao-img" src="../../../assets/images/index/orange/ribao.png" alt="">
          <span class="work_alias">{{item.project_name}}</span>
        </div>
        <div class="panel-content">
<!--          <div class="work-summary">-->
<!--            <span class="gray-span">工作项目:</span>-->
<!--            <div class="content-span one-line">{{item.work_intro}}</div>-->
<!--          </div>-->
          <div class="work-content">
            <span class="gray-span">工作内容:</span>
            <div class="content-span">{{item.work_matters}}</div>
          </div>
        </div>
        <div class="panel-bottom">
          <span class="work_date">
            <img src="../../../assets/images/index/gray/riqi.png" alt="">
            {{item.work_date}} 星期{{item.DayInWeek|| '?'}}
          </span>
          <span class="work_hour">
            <img src="../../../assets/images/index/gray/icon-test.png" alt="">
            {{item.work_hours}} 工时
          </span>
          <span class="work_week">
            <img src="../../../assets/images/index/gray/zhou.png" alt="">
            第{{item.Weeks|| '?'}}周
          </span>
        </div>
      </div>
      </van-list>
      <div class="panel" v-for="(item, index) in workDatas"
           v-show="thisWeek"
           :key="index" @click="showWorkDailyById(item.daily_id)">
        <div class="panel-title">
          <img class="ribao-img" src="../../../assets/images/index/orange/ribao.png" alt="">
          <span class="work_alias">{{item.project_name}}</span>
        </div>
        <div class="panel-content">
          <div class="work-content">
            <span class="gray-span">工作内容:</span>
            <div class="content-span">{{item.work_matters}}</div>
          </div>
        </div>
        <div class="panel-bottom">
          <span class="work_date">
            <img src="../../../assets/images/index/gray/riqi.png" alt="">
            {{item.work_date}} 星期{{item.DayInWeek|| '?'}}
          </span>
          <span class="work_hour">
            <img src="../../../assets/images/index/gray/icon-test.png" alt="">
            {{item.work_hours}} 工时
          </span>
          <span class="work_week">
            <img src="../../../assets/images/index/gray/zhou.png" alt="">
            第{{item.Weeks|| '?'}}周
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'xd-moment';
export default {
  name: 'work_daily',
  data() {
    return {
      thisWeek: false,
      loading: false,
      finished: false,
      loadingShow: false,
      dayNums: 30,
      startDate: moment().format('YYYY-MM-DD'), // 起始日期
      error: false,
      check: false,
      workDatas: [], // 保存所有工作数据
      topBckUrl: require('../../../assets/images/work/title-background.png'),
      currentDate: '' // 顶部当前时间
    };
  },
  computed: {
    dispalyWorkDaily() {
      return this.$store.state.displayWorkDaily;
    }
  },
  mounted() {
    this.$store.commit('resetWorkDaily');
    this.onLoad()
    this.handleCurrentDate();
  },
  methods: {
    onLoad() {
      var params = {
        end: this.startDate,
        start: moment().subtract('days', this.dayNums)
          .format('YYYY-MM-DD')
      };
      if (!this.check){
        this.workDailyHandle(params, false);
        this.startDate = moment().subtract('days', this.dayNums + 1)
          .format('YYYY-MM-DD');
        this.dayNums = this.dayNums + 30;
      } else {
        this.loading = false;
      }
    },
    workDailyHandle(obj, type) { // 显示工作日报
      this.$api.get('/iims/dailies/data?detail=true', obj).then( res => {
        var datas = res.data;
        var obj1 = [];
        var i = 0;
        this.loading = false;
        if (res.status === 1) {
          if (datas.length === 0){
            this.finished = true;
            return;
          }
          for (i; i < datas.length; i++) {
            this.$store.commit('storeAllWorkDaily', datas[i]); // 将数据保存到store中的allWorkDaily中
            this.$store.commit('storeDisplayWorkDaily', datas[i]);
            obj1.push(datas[i]); // 将数据逐一push到obj中
          }
          if (type){
            this.workDatas = obj1;
          }
        } else {
          this.$myToast('获取数据失败！' + res.message, 'error', '1500');
        }
      })
        .catch(err => {
          this.error = true;
          console.log(err);
        });
    },
    addWorkDaily() { // 处理添加工作日报
      this.$store.commit('changeJudgeAdd', true);
      this.$router.push({
        name: 'add_work_daily',
        query: {workDailyId: -1}
      });
    },
    showWorkDailyById(id) { // 进入工作日报详情页,获取点击的工作日报id
      this.$store.commit('changeJudgeAdd', false);
      this.$router.push({
        name: 'add_work_daily',
        query: {workDailyId: id}
      });
    },
    confirmDate(value) { // 格式化时间
      var m = value.getMonth() + 1;
      var d = value.getDate();
      var timer = value.getFullYear() + '-' + m + '-' + d; // 格式化的时间
      if (m >= 1 && m <= 9) {
        m = '0' + m;
      }
      if (d >= 0 && d <= 9) {
        d = '0' + d;
      }
      return timer;
    },
    handleCurrentDate() { // 处理当前时间
      this.currentDate = new Date();
      this.currentDate = this.confirmDate(this.currentDate);
    },
    showWeekFirstDay() { // 获取本周第一天
      var Nowdate = new Date();
      var WeekFirstDay = new Date(Nowdate - (Nowdate.getDay() - 1) * 86400000);
      var M = Number(WeekFirstDay.getMonth()) + 1;
      return WeekFirstDay.getFullYear() + '-' + M + '-' + WeekFirstDay.getDate();
    },
    showWeekLastDay() { // 获取本周最后一天
      var Nowdate = new Date();
      var WeekFirstDay = new Date(Nowdate - (Nowdate.getDay() - 1) * 86400000);
      var WeekLastDay = new Date((WeekFirstDay / 1000 + 6 * 86400) * 1000);
      var M = Number(WeekLastDay.getMonth()) + 1;
      return WeekLastDay.getFullYear() + '-' + M + '-' + WeekLastDay.getDate();
    }
  },
  watch: {
    check(data){ // 是否选中显示本周
      var weekFirstDay = this.showWeekFirstDay();
      var weekLastDay = this.showWeekLastDay();
      var obj = {
        start: weekFirstDay,
        end: weekLastDay
      };
      this.loadingShow = true;
      setTimeout( () => {
        this.loadingShow = false;
      }, 1000)
      if (!data) {
        this.thisWeek = false;
      } else {
        this.thisWeek = true
        this.workDailyHandle(obj, true);
      }
    }
  }
};
</script>

<style lang="less" scoped>
  .container{
    text-align: center;
    .top{
      width: 100%;
      height: 100px;
      background: url("../../../assets/images/work/title-background.png");
      background-size: cover;
      .show-week {
        display: flex;
        justify-content: space-between;
        .van-checkbox {
          margin-left: 20px;
          margin-top: 10px;
          /deep/.van-checkbox__label {
            color: #fff;
            margin-top: 0.1rem;
          }
        }
        span{
          margin-right: 20px;
          margin-top: 10px;
          color: #fff;
        }
      }
      .box{
        display: flex;
        flex-flow: column;
        width: 60%;
        height: auto;
        padding-top: 0.3rem;
        padding-bottom: 0.3rem;
        background-color: #fff;
        box-shadow: 0px 0px 16px #dedede;
        border-radius: 0.2rem;
        position: fixed;
        text-align: center;
        top: 1.6rem;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 1;
        img {
          margin: auto;
        }
        span{
          color: #7aafff;
          font-size: 0.7rem;
          letter-spacing: 4px;
        }
      }
    }
    .main{
      position: absolute;
      width: 100%;
      top: 20%;
      bottom: 50px;
      overflow-y: auto;
      text-align: center;
      .panel{
        display: flex;
        flex-flow: column;
        width: 86%;
        justify-content: space-between;
        /*height: 6rem;*/
        height: auto;
        margin: 20px auto;
        background-color: #fff;
        box-shadow: 1px 1px 5px #dedede;
        border-radius: 5px;
        overflow: hidden;
        text-align: left;
        .panel-content{
          padding: 0.1rem 0rem;
          width: 96%;
          margin-left: 2%;
          .work-summary, .work-content{
            display: flex;
          }
          .gray-span{
            width: 3rem;
            color: gray;
            font-size: 0.6rem;
          }
          .one-line{
            -webkit-line-clamp: 1 !important;
            line-clamp: 1 !important;
          }
          .content-span{
            width: calc(100% - 3rem);
            color: #181818;
            font-size: 0.6rem;
            /*overflow : hidden;*/
            /*text-overflow: ellipsis;*/
            /*display: -webkit-box;*/
            /*-webkit-line-clamp: 2;*/
            /*-webkit-box-orient: vertical;*/
          }
        }
        .panel-bottom{
          padding: 0.3rem 0rem;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          font-size: 0.55rem;
          color: gray;
          border-top: 1px solid gainsboro;
          width: 96%;
          display: flex;
          justify-content: space-between;
          margin-left: 2%;
          flex-direction: row;
          span{
            display: flex;
            align-items: center;
            img{
              margin-top: -0.1rem;
              margin-right: 0.2rem;
            }
          }
          img{
            width: 0.65rem;
            height: 0.65rem;
          }
          font-size: 0.55rem;
          color: gray;
        }
        .panel-title {
          display: flex;
          flex-flow: row;
          align-items: center;
          background-color: #f4f4f4;
          width: 100%;
          padding: 0.3rem;
          box-sizing: border-box;
          .ribao-img{
            width: 0.65rem;
            height: 0.65rem;
          }
          span {
            text-align: left;
            font-size: 0.6rem;
            margin-left: 0.3rem;
            white-space:nowrap;
            text-overflow:ellipsis;
            overflow:hidden;
          }
        }
        .work_item {
          margin-top: 10px;
          font-size: 14px;
          display: -webkit-box;
          overflow: hidden;
          white-space: normal !important;
          text-overflow: ellipsis;
          word-wrap: break-word;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical
        }
        .work_daily{
          margin-top: 10px;
          font-size: 14px;
          display: -webkit-box;
          overflow: hidden;
          white-space: normal !important;
          text-overflow: ellipsis;
          word-wrap: break-word;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical
        }
      }
    }
    .bottom{
      .van-tabbar {
        box-shadow: 0px -3px 11px 1px rgba(163, 163, 163, 0.49);
        .van-tabbar-item--active {
          color: #fe8f01;
        }
      }
    }
    .my-checkbox{
     /deep/ .van-checkbox__icon--checked .van-icon {
        color: #fe8f01;
        background-color: #ffffff;
        border-color: #ffffff;
       border-radius: 0px;
      }
      /deep/ .van-checkbox__icon--round .van-icon {
        border-radius: 0px;
      }
      /deep/ .van-checkbox__icon {
        font-size: 0.6rem;
      }
    }
    .my-loading{
      font-size: 0;
      position: fixed;
      background-color: #ffffff99;
      width: 100vw;
      height: 100vh;
      z-index: 99;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
</style>
